import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import '../../style/menuTitle.css'

class MenuTitle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      resturantName: "",
    }
  }

  componentDidMount() {
    this.loadTitle();
    // this.loadLink();

  }

  loadTitle() {
    let restName = new Promise((resolve, reject) => {
      let url = `api/rest-name`;
      const xhr = new XMLHttpRequest();
      xhr.open("GET", url, true);
      xhr.onload = () => {
        console.log(xhr.status);
        if (xhr.status == 200) {
          resolve(xhr.responseText);
        } else {
          reject()
        }
      }
      xhr.onerror = () => {console.log(xhr.status + "bad ");reject()};
      xhr.send();
    });

    restName.then((responseText) => {
      console.log
      this.setState({resturantName: responseText})
    }, (status) => {
      alert(`cannot get rest name`)
    })
  

  }

  // loadLink() {
  //   let url = "api/login/rest_id";

  //   fetch(url)
  //     .then(response => response.json())
  //     .then(json => {
  //       this.setState({ viewUrl: this.state.viewUrl + json });
  //     })
  // }

  render() {

    return (
      <div className="rest-title">
        <h1>{this.state.resturantName}</h1>


        {/* <Router>
          <Route exact path='/edit-menu'    render={() => (
            <div className="title-edit-items">
              <h3>edit your menu</h3> 
              <p>you can view the menu at <a href={this.state.viewUrl} target="_blank">Link</a></p>
            </div>
            
            )}/>
        </Router> */}


      </div>
    );
  }// end of render

}// end of MenuTitle
export default MenuTitle;



